.BPScrollPanel {
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  width: 100%;
  height: 100%;
  padding: 0px;
  margin-bottom: 0px;
  margin-right: 0px; }

.BPMousePanel {
  position: absolute;
  overflow: hidden; }

.BPPlaceholderPanel {
  position: absolute;
  overflow: hidden; }

.BPLayerPanel {
  position: absolute;
  overflow: visible; }

.BPCalloutPlaceholderPanel {
  position: absolute;
  overflow: visible; }

.BPItemPlaceholder {
